import { show,hide } from "../actionTypes"
// let showtop = false
export default (state:any = false,action:any) => {
// console.log()
    switch(action.type){
        case show://
        // console.log("show",state)
            return !state// state  this.props.state 
        case hide://
        // console.log("hide",state)
            return !state
        default:
            return state
    }
}


